import React, { useState, useRef } from 'react';
// import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';
import InputBase from '@material-ui/core/InputBase';
import Typography from '../extensions/Typography';
import Button from '../extensions/Button';
import ButtonM from '@material-ui/core/Button'
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import {connect} from 'react-redux';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import {NavLink,Link,withRouter} from 'react-router-dom';
import {logout} from '../../../actions'
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
const useStyles = makeStyles(({ transitions }) => ({
  searchInput: ({ searchAppeared }) => ({
    width: searchAppeared ? 180 : 0,
    opacity: searchAppeared ? 1 : 0,
    transition: transitions.create(),
    backgroundColor: 'rgba(255, 255, 255, 0.08)',
    marginTop: 8,
    marginLeft: 8,
    padding: '0 8px',
    display: 'inline-block',
    color: '#ffffff',
  }),
  secondAppBar: ({ trigger }) => ({
    zIndex: 1099,
    transition: transitions.create('top', {
      duration: transitions.duration.leavingScreen,
      easing: transitions.easing.easeOut,
    }),
    top: trigger ? 0 : 64,
  }),
  appBarBg: {
    zIndex: 1098,
  },
}));

const AmiLargeHeader = ({cartLength,isAuth,history,logout}) => {


  const [anchorEl, setAnchorEl] = React.useState(null);


  const isMenuOpen = Boolean(anchorEl);


  function handleProfileMenuOpen(event) {
    console.log('handle')
    setAnchorEl(event.currentTarget);
  }



  function handleMenuClose() {
    setAnchorEl(null);

  }

  function handleLogout(){
    document.getElementsByClassName("logout")[0].click()
    logout()
    history.push('/')
    handleMenuClose()

  }


  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
     <Link to={'/user/profile'} style={{color:'black'}}> <MenuItem onClick={handleMenuClose}>Profile</MenuItem></Link>

   <Link to={'/user/orders'} style={{color:'black'}}><MenuItem onClick={handleMenuClose}>My Orders</MenuItem></Link>
   <MenuItem onClick={handleLogout}>Logout</MenuItem>
    </Menu>
  );



  const trigger = useScrollTrigger();
  const inputRef = useRef(null);
  const [searchAppeared, setSearchAppeared] = useState(false);
  const classes = useStyles({ searchAppeared, trigger });
  const handleSearchClick = () => {
    setSearchAppeared(!searchAppeared);
    if (!searchAppeared && inputRef.current) {
      inputRef.current.focus();
    }
  };
  return (
    <>

        <AppBar>
          <Toolbar>
            <Grid container wrap={'nowrap'} alignItems={'center'}>
            <Grid item xs container >
               <Typography variant={'h5'} style={{color:'white'}} weight={'900'} spacing={'big'} onClick={()=>history.push('/')}>
                  GREEN PLANET
                </Typography>
              </Grid>
              <Grid item xs>
                <Button

                  className={'fa fa-search'}
                  shape={'circular'}
                  inverted
                  onClick={handleSearchClick}
                />
                <InputBase
                  inputRef={inputRef}
                  className={classes.searchInput}
                />
              </Grid>

              <Grid item xs>
                <Grid
                  container
                  alignItems={'center'}
                  justify={'flex-end'}
                  spacing={2}
                  wrap={'nowrap'}
                >
                  <Grid item>
                  {isAuth?
                    <IconButton
                    edge="end"
                    aria-label="account of current user"
                    aria-controls={menuId}
                    aria-haspopup="true"
                    onClick={handleProfileMenuOpen}
                    style={{color:'white'}}

                  >
                    <AccountCircle />
                  </IconButton>:




                    <NavLink className="nav-link" to={"/login"}><ButtonM style={{color:'white'}}>Login</ButtonM></NavLink>}
                  </Grid>

                  <Grid item>
                  <NavLink className="nav-link" to={"/cart"}> <IconButton style={{color:'white'}} ><Badge  badgeContent={cartLength} color={'secondary'} >  <ShoppingCartIcon/></Badge></IconButton></NavLink>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
    {renderMenu}

    </>
  );
};

AmiLargeHeader.propTypes = {};
AmiLargeHeader.defaultProps = {};
const mapStateToProps = (state) => {
  return {
      cartLength: state.shop.cart.length,
      isAuth:state.Auth.isAuth
  }
};

export default withRouter(connect(mapStateToProps,{logout})(AmiLargeHeader))
